import Vue from 'vue'
import Router from 'vue-router'
import MainPage from '@/page/main'
import JoinPage from '@/page/join'
import LoginPage from '@/page/login'
import QNA from '@/page/qna'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'MainPage',
      component: MainPage
    },
    {
      path: '/join',
      name: 'JoinPage',
      component: JoinPage
    },
    {
      path: '/login',
      name: 'LoginPage',
      component: LoginPage
    },
    {
      path: '/qna',
      name: 'QNA',
      component: QNA
    }
  ]
})



