import _ from 'lodash'
import axios from 'axios'
import apiUrl from '@/util/apiurl'

let checkResCode = res => {
  if( _.get( res, 'data.code' ) === 200 ) return _.get( res, 'response' )
  else return Promise.reject( _.get( res, 'data' ) )
}

export default {
  login( { id, password } ) {
    return axios.post( apiUrl( '/member/login' ), {
      id, password
    } ).then( x => checkResCode( x ) )
  }
}



