<template>
  <div class="main-page-container">
    <div class="header">
      <div class="mark">OKKY</div>
      <div class="router">
        <div class="menu" @click="joinQNA">Q&A</div>
        <div class="menu">지식</div>
        <div class="menu">커뮤니티</div>
        <div class="menu">이벤트</div>
      </div>
      <div class="login">
        <div class="menu" @click="onLogin">로그인</div>
        <div class="menu registry" @click="onRegister">회원가입</div>
      </div>
    </div>
    <div class="content">
      <div class="side">
      <div class="ranker-title">Top Writer</div>
      <div class="ranker">
        <div class="name">김한솔</div>
        <div class="score">80</div>
      </div><div class="ranker">
        <div class="name">김한솔</div>
        <div class="score">80</div>
      </div><div class="ranker">
        <div class="name">김한솔</div>
        <div class="score">80</div>
      </div><div class="ranker">
        <div class="name">김한솔</div>
        <div class="score">80</div>
      </div><div class="ranker">
        <div class="name">김한솔</div>
        <div class="score">80</div>
      </div>
    </div>
    <div class="quaters-content">
      <div class="qna-container">
        <div class="title">Q&A</div>
      </div>
      <div class="community-container">
        <div class="title">커뮤니티</div>
      </div>
      <div class="weekly-container">
        <div class="title">WEEKLY BEST</div>
      </div>
      <div class="choice-container">
        <div class="title">EDITOR CHOICE</div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import req2svr from './req2svr'

export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  computed: {
    req2svr: () => req2svr,
  },
  created() {
    this.req2svr.getInitData()
  },
  methods: {
    onRegister() {
      this.$router.push( '/join' )
    },
    onLogin() {
      this.$router.push( '/login' )
    },
    joinQNA() {
      this.$router.push( 'qna' )
    }
  }

}
</script>

<style lang="scss" scoped>
  .main-page-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 0 100px;
  }

  .header {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    height: 65px;
    border-bottom: 1px solid #333333;

    .mark {
      display: flex;
      align-items: center;
      font-size: 20px;
      font-weight: bold;
    }

    .router {
      display: flex;
      align-items: center;

      .menu {
        margin-right: 10px;
        font-weight: bold;
      }
    }

    .login {
      display: flex;
      justify-content: center;
      align-items: center;

      .menu {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 10px;
        border-radius: 50px;
        width: 80px;
        height: 40px;
        border: 1px solid #333333;
        cursor: pointer;

        &.registry {
          background-color: blue;
          border: none;
          color: #ffffff;
        }
      }
    }


  }

  .content {
    display: flex;
    flex-direction: row;

    
    .side {
      flex: 0 1 200px;

      .ranker-title {
        font-size: 18px;
        font-weight: bold;
        padding-bottom: 30px;
        border-bottom: 1px solid #333333;
      }
      

      .ranker {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 10px;
      }
    }

    .quaters-content {
      flex: 1 1 300px;

      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 1fr);
    }
  }

  
</style>



